<template>
    <div class="container">
        <div class="item">
            <div class="item__left">
                <h2>{{ getCar.name }}</h2>
                <p>{{ getCar.text }}</p>
            </div>
            <div class="item__right">
                <img :src="getCar.img" alt="">
            </div>
            <router-link to="/cars">Назад</router-link>
        </div>
    </div>
</template>

<script>
import { items } from '@/data'
    export default {
        data() {
            return {
                cars: items,
                currentId: null
            }
        },
        created() {
           this.currentId = this.$route.params.id
        },
        computed: {
            getCar() {
                return this.cars.find(item => item.id == this.currentId)
            }
        }
    }
</script>

